import React, { memo, useCallback, useEffect } from 'react'

import PageLayout from 'src/components/PageLayout'
import FullLoader from 'src/components/FullLoader'

import Info from './components/Info'
import UserTable from './components/UserTable'

import { useMappedActions, useMappedState } from './bindings'

import styles from './styles.module.scss'

const UserRaw = ({ match }) => {
  const { info } = useMappedState()
  const actions = useMappedActions()
  const { url, status, isLoading } = info
  const isEnable = status === 'enabled'

  const userId = match.params.userId

  useEffect(() => {
    actions.loadAffiliate(userId)
  }, [actions, userId])

  const onClickLock = useCallback(async () => {
    if (isEnable) {
      await actions.disableAffiliate(userId)
    } else {
      await actions.enableAffiliate(userId)
    }

    actions.loadAffiliate(userId)
  }, [actions, isEnable, userId])

  if (isLoading) {
    return <FullLoader />
  }

  return (
    <PageLayout backRoute={'/statistics'} pageTitle={'User info'}>
      <div className={styles.section}>
        <Info info={info} onClickLock={onClickLock} />
        <UserTable url={url} userId={userId} />
      </div>
    </PageLayout>
  )
}

export const User = memo(UserRaw)
export default User
