import { useMapActions, useMapState } from 'src/hooks'
import { affiliatesThunks } from 'src/thunks'
import { affiliateSelectors } from 'src/selectors'

export const useMappedState = () => {
  return useMapState((state) => {
    return {
      metrics: affiliateSelectors.getAffiliateMetrics(state),
    }
  })
}

export const useMappedActions = () => {
  return useMapActions({
    loadAffiliateStats: affiliatesThunks.loadAffiliateStats,
  })
}
