import React, { Fragment, memo, useMemo, useState, useEffect } from 'react'
import moment from 'moment'

import Download from 'src/components/Download'
import Table from 'src/indyComponents/Table'
import DateRangePicker from 'src/indyComponents/DateRangePicker'

import * as services from '../../services'
import styles from './styles.module.scss'

import { useMappedActions, useMappedState } from './bindings'

const UserTableRaw = ({ url, userId }) => {
  const { metrics } = useMappedState()
  const actions = useMappedActions()
  const { items } = metrics

  const [filter, setFilter] = useState({
    from: moment()
      .subtract(29, 'days')
      .format('YYYY-MM-DD'),
    to: moment().format('YYYY-MM-DD'),
  })

  useEffect(() => {
    actions.loadAffiliateStats(userId, filter)
  }, [actions, userId, filter])

  const { columns, data } = useMemo(() => {
    return services.makeTable(items)
  }, [items])

  const onChangeDate = (value) => {
    const { from, to } = value
    const fromNext = from.format('YYYY-MM-DD')
    const toNext = to.format('YYYY-MM-DD')
    setFilter({ from: fromNext, to: toNext })
  }

  return (
    <Fragment>
      <div className={styles.sectionHead}>
        <div className={styles.left}>
          <div className={styles.sectionTitle}>User brands</div>
          <div className={styles.sectionLabel}>
            <Download url={url} />
          </div>
        </div>
        <div className={styles.sectionRight}>
          <DateRangePicker onChangeDate={onChangeDate} />
        </div>
      </div>

      <div className={styles.sectionBody}>
        <Table columns={columns} data={data} />
      </div>
    </Fragment>
  )
}

export const UserTable = memo(UserTableRaw)
export default UserTable
