import React, { memo, Fragment, useMemo } from 'react'
import cn from 'classnames'

import BaseButton from 'src/components/BaseButton'

import { ReactComponent as EnabledIcon } from 'src/assets/images/icons/enabled.svg'
import { ReactComponent as DisabledIcon } from 'src/assets/images/icons/disabled.svg'

import styles from './styles.module.scss'

const InfoRaw = ({ info, onClickLock }) => {
  const { id, name, status, contacts } = info
  const { email, phone, telegram } = contacts
  const { first, last } = name

  const isEnable = status === 'enabled'

  const renderStatus = useMemo(() => {
    if (isEnable) {
      return (
        <Fragment>
          Unlock <EnabledIcon />
        </Fragment>
      )
    }
    return (
      <Fragment>
        Lock <DisabledIcon />
      </Fragment>
    )
  }, [isEnable])

  return (
    <div className={styles.info}>
      <div className={styles.left}>
        <div className={styles.item}>
          <div className={styles.label}>Name</div>
          <div className={styles.value}>
            {first} {last}
          </div>
        </div>
        <div className={styles.item}>
          <div className={styles.label}>User ID</div>
          <div className={styles.value}>{id}</div>
        </div>
        <div className={styles.item}>
          <div className={styles.label}>E-mail</div>
          <div className={styles.value}>{email}</div>
        </div>
        <div className={styles.item}>
          <div className={styles.label}>Phone number</div>
          <div className={styles.value}>{phone}</div>
        </div>
        <div className={styles.item}>
          <div className={styles.label}>Telegram</div>
          <div className={styles.value}>{telegram}</div>
        </div>
      </div>
      <div className={styles.right}>
        <div className={cn(styles.item, styles.status)}>
          <div className={styles.label}>Status</div>
          <div
            className={cn(styles.value, {
              [styles.enable]: isEnable,
              [styles.disable]: !isEnable,
            })}
          >
            {renderStatus}
          </div>
        </div>

        <div className={styles.item}>
          <BaseButton
            text={isEnable ? 'Lock' : 'Unlock'}
            skin="yellow"
            disabled={false}
            onClick={onClickLock}
            className={styles.btn}
          />
        </div>
      </div>
    </div>
  )
}

export const Info = memo(InfoRaw)
export default Info
