const getColumns = () => {
  return [
    {
      name: 'Brand',
      key: 'BRAND',
      width: '18.09%',
      isSearch: true,
    },
    {
      name: 'Clicks',
      key: 'CLK',
      width: '7.59%',
    },
    {
      name: 'Lead',
      key: 'LDS',
      width: '6.87%',
    },
    {
      name: 'Approved leads',
      key: 'APV',
      width: '15.84%',
    },
    {
      name: 'EPC',
      key: 'EPC',
      width: '6.43%',
    },
    {
      name: 'EPL',
      key: 'EPL',
      width: '6.25%',
    },
    {
      name: 'Revenue',
      key: 'RVN',
      width: '9.46%',
    },
  ]
}

const getData = (items) => {
  return items.reduce((rows, row, rowId) => {
    const { key, metrics } = row

    rows[rowId] = Object.keys(metrics).reduce((cells, cellKey) => {
      cells.push({
        value: metrics[cellKey],
        key: cellKey,
        type: 'TEXT',
      })

      return cells
    }, [])

    rows[rowId].unshift({
      value: key,
      key: 'BRAND',
      type: 'TEXT',
    })
    return rows
  }, {})
}

const makeTable = (items) => {
  return {
    columns: getColumns(items),
    data: getData(items),
  }
}

export default makeTable
